.btn-mini,
.btn-small {
  overflow: visible;
  cursor: pointer;
  outline: 0;
  display: inline-block;
  border-radius: 3px;
  text-decoration: none;
  text-align: center;
  vertical-align: top;
  transition: 0.3s; }

.btn-mini {
  height: 22px;
  min-width: 24px;
  _width: 24px;
  padding: 0 5px; }

.btn-small {
  height: 16px; }

.ui-btn-small {
  min-width: 48px;
  _width: 48px;
  padding: 0 11px;
  height: 22px; }

.btn-primary {
  background-color: #f10180;
  border: 1px solid #f10180;
  color: #fafafa; }

.btn-default {
  background-color: #fafafa;
  border: 1px solid #c2c2c2;
  color: #333333; }

.ui-checkbox {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
  font-size: 0;
  line-height: 0; }

.ui-checkbox-normal {
  display: none; }

.ui-checkbox-simulation {
  display: block;
  background-image: url(//bootstrap.vipstatic.com/img/ui/sprite.png?d9eadd17);
  background-position: -48px 0;
  width: 19px;
  height: 16px; }
  .ui-checkbox-simulation:hover {
    background-position: -24px -21px; }

.ui-quantity {
  display: inline-block;
  width: 73px;
  height: 22px;
  background-color: #fff;
  border: 1px solid #e3e2e2;
  color: #333;
  vertical-align: top;
  text-align: center;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out; }

.ui-btn-loading,
button.ui-btn-loading {
  padding: 0;
  overflow: hidden; }

button.ui-btn-big,
button.ui-btn-large {
  height: 46px; }

.ui-btn-primary {
  background-color: #f10180;
  border: 1px solid #f10180; }

.ui-btn-primary,
.ui-btn-loading-before {
  color: #fff; }

.ui-btn-loading-before {
  display: inline-block; }

.ui-btn-big,
.ui-btn-primary {
  font-size: 18px;
  line-height: 44px;
  height: 44px; }

.ui-tooltips {
  transition: transform ease-out 0.15s, opacity ease-out 0.15s;
  transform: translateY(10px); }

.ui-tips-pop,
.ui-tooltips {
  position: absolute;
  max-width: 800px;
  border: 1px solid #dbdada;
  border-radius: 2px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  font-size: 12px;
  line-height: 18px;
  color: #4d4d4d;
  z-index: 50; }

.ui-tooltips-arrow {
  left: 0;
  top: -11px;
  width: 100%;
  height: 10px; }

.ui-tooltips-content {
  position: relative;
  background-color: #fff;
  padding: 6px 15px; }
